function rdmNum(min, max) {
    var range = max - min;
    var rand = Math.random();
    return min + Math.round(rand * range); //四舍五入
}

function rdmRed() {
    const num = rdmNum(1, 33)
    if (num < 10) {
        return "0" + num
    }
    return num.toString()
}

function rdmBlue() {
    const num = rdmNum(1, 16)
    if (num < 10) {
        return "0" + num
    }
    return num.toString()
}

const Util = function () {

}

Util.prototype.rdmRedBalls = function () {
    const size = 6;
    const arr = []

    while (arr.length < size) {
        const red = rdmRed()
        if (arr.indexOf(red) == -1) {
            arr.push(red)
        }
    }

    arr.sort(function (v1, v2) {
        return v1 - v2
    })

    return arr
}

Util.prototype.rdmBlueBall = function () {
    return rdmBlue()
}


const util = new Util()

export default util