<template>

    <div class="red-root">


        <div class="ball"
             :class="ball.selected ? 'selected': ''"
             v-for="ball in balls" @click="clickBall(ball)">
            {{ball.number}}
        </div>


    </div>

</template>

<script>
    export default {
        name: "RedBallPicker",
        props: {
            data: {
                require: false,
                type: Array,
                default: []
            }
        },
        data() {
            return {
                balls: [{number: "01", selected: false}, {number: "02", selected: false}, {
                    number: "03",
                    selected: false
                }, {number: "04", selected: false}, {number: "05", selected: false}, {
                    number: "06",
                    selected: false
                }, {number: "07", selected: false}, {number: "08", selected: false}, {
                    number: "09",
                    selected: false
                }, {number: "10", selected: false}, {number: "11", selected: false}, {
                    number: "12",
                    selected: false
                }, {number: "13", selected: false}, {number: "14", selected: false}, {
                    number: "15",
                    selected: false
                }, {number: "16", selected: false}, {number: "17", selected: false}, {
                    number: "18",
                    selected: false
                }, {number: "19", selected: false}, {number: "20", selected: false}, {
                    number: "21",
                    selected: false
                }, {number: "22", selected: false}, {number: "23", selected: false}, {
                    number: "24",
                    selected: false
                }, {number: "25", selected: false}, {number: "26", selected: false}, {
                    number: "27",
                    selected: false
                }, {number: "28", selected: false}, {number: "29", selected: false}, {
                    number: "30",
                    selected: false
                }, {number: "31", selected: false}, {number: "32", selected: false}, {number: "33"}],
                selectedBalls: []
            }
        },
        watch: {
            data(newVal, oldVal) {
                if (newVal != null && newVal.length > 0 && newVal.length <= 6) {
                    this.selectedBalls = []
                    this.selectedBalls.push(...newVal)

                    const self = this
                    this.balls.forEach(function (item) {
                        item.selected = self.selectedBalls.indexOf(item.number) != -1
                    })

                    this.$emit('onSelected', this.selectedBalls)
                }
            }
        },
        methods: {
            clickBall(ball) {
                if (ball.selected) {
                    //已经选了，取消选中
                    ball.selected = false

                    const index = this.selectedBalls.indexOf(ball.number)
                    this.selectedBalls.splice(index, 1)

                    this.$emit('onSelected', this.selectedBalls)
                } else {
                    //没有选中
                    //如果已经选中6个，不能继续选中
                    if (this.selectedBalls.length >= 6) return

                    ball.selected = true
                    this.selectedBalls.push(ball.number)

                    this.$emit('onSelected', this.selectedBalls)
                }
            }
        }
    }
</script>

<style scoped>

    .red-root {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        flex-flow: wrap;
        /*justify-content: space-around;*/
        padding-left: 1rem;
    }

    .ball {
        width: 2.5rem;
        height: 2.5rem;
        display: flex;
        align-items: center;
        justify-content: center;
        border: .12rem solid rgba(187, 187, 187, 1);
        border-radius: 1.25rem;
        margin-right: 1rem;
        margin-top: 1rem;
        color: rgba(229, 28, 35, 1);
    }

    .ball.selected {
        color: white;
        background-color: rgba(229, 28, 35, 1);
        transition: all .4s;
    }


</style>