<template>
    <div class="shopping-root">
        <title-bar
                :back="true"
                :title="'双色球'"
                @onBack="onBack"></title-bar>

        <div class="shopping-content">

            <div class="tip">
                <span>第2018017期</span>
                <span>奖池4亿1523万,可开出83注500万</span>
            </div>

            <p class="info">请选择6个红球，1个蓝球</p>

            <red-ball-picker :data="rdmRedBalls" @onSelected="onRedBallSelected"></red-ball-picker>

            <blue-ball-picker :data="rdmBlueBall" @onSelected="onBlueBallSelected"></blue-ball-picker>

            <div class="btn-add-container">
                <div class="btn-add" @click="addOne">添加</div>
            </div>

            <div class="btn-rdm-container">
                <select class="select" v-model="num">
                    <option value="1">1</option>
                    <option value="2">2</option>
                    <option value="3">3</option>
                    <option value="4">4</option>
                    <option value="5">5</option>
                </select>

                <div class="btn-rdm" @click="addRdm">机选幸运号</div>

            </div>

        </div>

        <div class="shopping-footer">

            <div class="footer-item" @click="clickCart">
                <icon name="shopping-cart" scale="1.5"></icon>
                <span>购物车</span>
                <span class="count">{{cartSize}}</span>
            </div>

            <div class="footer-item" @click="clickCart">
                <icon name="download" scale="1.5"></icon>
                <span>立即投注</span>
            </div>

        </div>

    </div>
</template>

<script>
    import TitleBar from "../../components/TitleBar";
    import RedBallPicker from "../../components/RedBallPicker";
    import BlueBallPicker from "../../components/BlueBallPicker";
    import Icon from "vue-awesome/components/Icon";
    import BallUtils from "../../utils/BallUtils"

    import {mapActions, mapGetters} from 'vuex'

    export default {
        name: "ShoppingTab",
        components: {Icon, BlueBallPicker, RedBallPicker, TitleBar},
        mounted() {
            this.selectTab(1)
            this.enableTab(false)
        },
        data() {
            return {
                num: 1,
                redBalls: [],
                blueBall: null,
                rdmRedBalls: [],
                rdmBlueBall: null
            }
        },
        created() {
            this.$store.dispatch("loadCart")
        },
        computed: {
            ...mapGetters({
                cartCount: 'cartCount'
            }),
            cartSize() {
                if (this.cartCount >= 100) {
                    return "99+"
                }

                return this.cartCount.toString();
            }
        },
        methods: {
            ...mapActions({
                selectTab: 'selectTab',
                enableTab: 'enableTab',
                addToCart: 'cartAdd'
            }),
            onBack() {
                this.$router.go(-1)
            },
            onRedBallSelected(balls) {
                this.redBalls = balls
            },
            onBlueBallSelected(ball) {
                this.blueBall = ball
            },
            addOne() {
                if (this.blueBall == null && this.redBalls.length == 0) {
                    this.rdmBlueBall = BallUtils.rdmBlueBall();
                    this.rdmRedBalls = BallUtils.rdmRedBalls();

                    const self = this
                    setTimeout(() => {
                        self.addToCart({
                            redBalls: self.redBalls, blueBall: self.blueBall, count: 1
                        })
                    }, 500)
                } else if (this.redBalls.length != 6 || this.blueBall == null) {
                    alert("红球只选中了" + this.redBalls.length)
                } else {
                    const self = this
                    setTimeout(() => {
                        self.addToCart({
                            redBalls: self.redBalls, blueBall: self.blueBall, count: 1
                        })
                    }, 500)
                }
            },
            addRdm() {
                this.rdmBlueBall = BallUtils.rdmBlueBall();
                this.rdmRedBalls = BallUtils.rdmRedBalls();

                const self = this
                setTimeout(() => {
                    self.addToCart({
                        redBalls: self.redBalls, blueBall: self.blueBall, count: self.num
                    })
                }, 500)
            },
            clickCart() {
                this.$router.push({name: 'cart'})
            }
        }
    }
</script>

<style scoped>

    .shopping-root {
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
    }

    .shopping-content {
        width: 100%;
        height: 100%;
        overflow-y: auto;
        overflow-x: hidden;
    }

    .tip {
        border: .1rem solid #333333aa;
        display: flex;
        justify-content: space-between;
        padding: .2rem;
    }

    .info {
        font-size: 1.2rem;
        margin: .5rem;
    }

    .btn-add-container {
        margin-top: 1rem;
        width: 94%;
        display: flex;
        align-items: center;
        justify-content: flex-end;
    }

    .btn-add {
        width: 6rem;
        height: 2rem;
        border: .2rem solid rgba(187, 187, 187, 1);
        border-radius: .4rem;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 1.2rem;
    }

    .btn-add:active {
        color: white;
        background-color: dodgerblue;
    }

    .btn-rdm-container {
        margin-top: 1rem;
        width: 94%;
        display: flex;
        align-items: center;
        justify-content: flex-end;
    }

    .btn-rdm {
        width: 9rem;
        height: 2rem;
        border: .2rem solid rgba(187, 187, 187, 1);
        border-radius: .4rem;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 1.2rem;
    }

    .btn-rdm:active {
        color: white;
        background-color: dodgerblue;
    }

    .select {
        margin-right: 1rem;
        width: 6rem;
        height: 2.2rem;
    }

    .shopping-footer {
        width: 100%;
        height: 4.5rem;
        border: .2rem none #33333333;
        border-top-style: solid;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .footer-item {
        width: 50%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        position: relative;
    }

    .footer-item:active {
        background-color: #33333333;
    }

    .count {
        position: absolute;
        background-color: red;
        color: white;
        border-radius: 1rem;
        padding: .2rem .5rem;
        top: 0;
        margin-left: 1.2rem;
    }

</style>