<template>

    <div class="blue-root">

        <div class="ball"
             :class="ball.selected ? 'selected': ''"
             v-for="ball in balls" @click="clickBall(ball)">
            {{ball.number}}
        </div>


    </div>

</template>

<script>
    export default {
        name: "BlueBallPicker",
        props: {
            data: {
                require: false,
                type: String,
                default: null
            }
        },
        data() {
            return {
                balls: [{number: "01", selected: false}, {number: "02", selected: false}, {
                    number: "03",
                    selected: false
                }, {number: "04", selected: false}, {number: "05", selected: false}, {
                    number: "06",
                    selected: false
                }, {number: "07", selected: false}, {number: "08", selected: false}, {
                    number: "09",
                    selected: false
                }, {number: "10", selected: false}, {number: "11", selected: false}, {
                    number: "12",
                    selected: false
                }, {number: "13", selected: false}, {number: "14", selected: false}, {
                    number: "15",
                    selected: false
                }, {number: "16", selected: false}],
                selectedBall: null
            }
        },
        watch: {
            data(newVal, oldVal) {
                if (newVal != null) {
                    this.selectedBall = newVal

                    this.balls.forEach(function (item) {
                        item.selected = item.number == newVal
                    })

                    this.$emit("onSelected", this.selectedBall)
                }
            }
        },
        methods: {
            clickBall(ball) {
                if (ball.selected) {
                    //已经选了，取消选中
                    ball.selected = false

                    this.selectedBall = null

                    this.$emit("onSelected", this.selectedBall)
                } else {
                    ball.selected = true
                    //没有选中, 选中当前
                    if (this.selectedBall != null) {
                        this.selectedBall.selected = false
                    }
                    this.selectedBall = ball

                    this.$emit("onSelected", this.selectedBall)
                }
            }
        }
    }
</script>

<style scoped>

    .blue-root {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        flex-flow: wrap;
        /*justify-content: space-around;*/
        padding-left: 1rem;
    }

    .ball {
        width: 2.5rem;
        height: 2.5rem;
        display: flex;
        align-items: center;
        justify-content: center;
        border: .12rem solid rgba(187, 187, 187, 1);
        border-radius: 1.25rem;
        margin-right: 1rem;
        margin-top: 1rem;
        color: rgba(0, 0, 255, 1);
    }

    .ball.selected {
        color: white;
        background-color: rgba(0, 0, 255, 1);
        transition: all .4s;
    }


</style>